import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../pages/Main.vue'
import Trasser from '../pages/Trasser.vue'
import Reagent from '../pages/Reagent.vue'
import Ecology from '../pages/Ecology.vue'
import Research from '../pages/Research.vue'

Vue.use(VueRouter)

const routes = [
    {path: '/', component: Main},
    {path: '/trasser', component: Trasser},
    {path: '/reagent', component: Reagent},
    {path: '/ecology', component: Ecology},
    {path: '/research', component: Research},
]

export default new VueRouter({
    // mode: 'history',
    routes,
    scrollBehavior (to, from, savedPosition) {
        console.log(to);
        console.log(from);
        console.log(savedPosition);
        return { x: 0, y: 0 }
    }
})